$primaryColor: #146EF5;
$secondaryColor: #FF7A00;
$titleColor: #070127;
$contentColor: #485781;

.bPlDashboardOverview {
	display: flex;

	.overviewLeft {
		display: flex;
		flex-direction: column;
		flex-basis: 1;

		.overviewLeftTop {
			padding: 25px 30px;
		}

		.overviewTitle {
			font-size: 32px;
			font-weight: 600;
			color: $titleColor;
			margin-bottom: 10px;
		}

		.overviewDescription {
			font-size: 18px;
			color: $contentColor;
			margin-bottom: 20px;
		}

		.overviewBanner {
			max-width: 100%;
			height: auto;
			display: grid;
			border-radius: 10px;
			overflow: hidden;

			img {
				max-width: 100%;
				height: auto;
				width: 100%;
			}
		}

		.overviewPluginInfo {
			margin-top: 25px;
			display: flex;
			align-items: center;
			gap: 15px;

			img {
				width: 75px;
				height: 75px;
				border-radius: 50%;
			}

			div {
				h3 {
					font-size: 18px;
					font-weight: 500;
					line-height: 1.3;
					color: $titleColor;
					margin-bottom: 5px;
				}

				span {
					font-size: 16px;
					font-weight: 500;
					text-transform: capitalize;
					color: $contentColor;
					margin-right: 5px;
				}

				a {
					color: $primaryColor;
					font-size: 16px;
					font-weight: 500;
					text-decoration: underline;
				}

			}
		}

		.overviewLeftBottom {
			justify-content: right;
			padding: 20px 30px;
			border-top: 1px solid rgba($contentColor, 50%);
			margin-top: auto;
		}
	}

	.overviewRight {
		min-width: 320px;
		border-left: 1px solid rgba($contentColor, 50%);

		.helpInfoItem {
			padding: 25px;

			&:not(:last-child) {
				border-bottom: 1px solid rgba($contentColor, 50%);
			}

			h4 {
				font-size: 18px;
				font-weight: 500;
				color: $titleColor;
				margin-bottom: 5px;
			}

			p {
				font-size: 15px;
				font-weight: 400;
				color: $contentColor;
				margin-bottom: 20px;
			}
		}
	}
}