$demoCol: var(--bpl-admin-demo-col, 3);
$itemHeight: var(--bpl-admin-demo-item-height, 270px);
$titleColor: #070127;

.bPlDashboardFilterDemos {
	padding: 15px;

	.demoHeader {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		gap: 20px;
		padding: 0 0 15px 0;
	}

	.filteredItems {
		display: grid;
		grid-template-columns: repeat($demoCol, 1fr);
		gap: 20px;

		.filterItem {
			.filterItemImg {
				height: $itemHeight;
				width: 100%;
				object-fit: cover;
				cursor: pointer;
			}
		}

		p {
			font-size: 15px;
			font-weight: 500;
			color: $titleColor;
			padding: 5px 0;
		}
	}
}

.bPlDashboardFilterDemosModal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.9);
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 99999;

	.filterDemoModal {
		position: relative;
		min-width: 350px;
		max-width: 90vw;
		max-height: 90vh;
		background: #fff;
		display: flex;
		flex-direction: column;
		padding-bottom: 68px;

		.modalClose {
			position: absolute;
			top: 10px;
			right: 10px;
			width: 30px;
			height: 30px;
			cursor: pointer;
			z-index: 10;
			transition: transform 0.2s;
			filter: drop-shadow(0px 0px 6px black);

			&:hover {
				transform: scale(1.1);
			}

			svg {
				width: 100%;
				height: 100%;
				fill: #fff;
			}
		}

		.modalNav {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			background: rgba(0, 0, 0, 0.5);
			color: white;
			border: none;
			font-size: 30px;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 50px;
			height: 50px;
			border-radius: 50%;
			cursor: pointer;
			z-index: 10;
			transition: background 0.2s;

			&:hover {
				background: rgba(0, 0, 0, 0.8);
			}

			svg {
				height: 20px;
				width: 20px;
				fill: #fff;
			}

			&.navPrev {
				left: 20px;
			}

			&.navNext {
				right: 20px;
			}
		}

		img {
			width: 100%;
			height: 100%;
			max-height: calc(90vh - 68px);
			object-fit: contain;
		}

		.modalFooter {
			color: $titleColor;
			background: linear-gradient(135deg, #18d4fd14, #0040e314);
			// backdrop-filter: blur(3px);
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 10px;
			position: absolute;
			right: 0;
			bottom: 0;
			left: 0;

			p {
				max-width: calc(100% - 215px);
				font-size: 14px;
				font-weight: 500;
				color: $titleColor;
				padding: 5px 0;
			}
		}
	}
}