$primaryColor: #146EF5;
$secondaryColor: #FF7A00;
$titleColor: #070127;
$contentColor: #485781;

.bPlDashboardPricing {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 20px 15px;

	.cycles {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		border: 1px solid #b3d5fd;
		border-radius: 10px;
		overflow: hidden;
		margin-bottom: 25px;

		button {
			font-size: 15px;
			font-weight: 700;
			background-color: #0000;
			padding: 10px 20px;
			border: 0;
			cursor: pointer;

			span {
				color: #fff;
				background-color: #0b8e41;
				padding: 4px 10px;
				border-radius: 50px;
				line-height: 1.3;
				margin-left: 8px;
			}

			&:not(:last-child) {
				border-right: 1px solid #b3d5fd;
			}

			&.active {
				background-color: #e1efff;
			}
		}

		@media only screen and (max-width: 640px) {
			flex-direction: column;

			button {
				width: 100%;

				&:not(:last-child) {
					border-right: none;
					border-bottom: 1px solid #b3d5fd;
				}
			}
		}

	}

	h2.pricingTitle {
		font-size: 18px;
		font-weight: 700;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		background-color: #e1efff;
		padding: 12px 32px;
		border: 1px solid #b3d5fd;
		border-radius: 10px;
		margin-bottom: 25px;
	}

	.plans {
		width: 100%;
		max-width: 100%;
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(min(380px, 100%), 1fr));
		container-type: inline-size;
		gap: 20px;
		padding: 0;

		.plan {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			background-color: #fff;
			padding: 25px 35px 25px 25px;
			border: 1px solid #e7e7ec;
			border-radius: 5px;
			margin: 0;

			.planName {
				margin-bottom: 20px;
			}

			.priceWrap {
				color: $primaryColor;
				display: flex;
				flex-wrap: nowrap;
				justify-content: flex-start;
				align-items: center;
				gap: 15px;
				margin-bottom: 20px;

				.price {
					font-size: clamp(30px, 30px + ((1vw - 3.2px) * 2.438), 56px);
					font-style: normal;
					font-weight: 700;
					line-height: 1.1;
					color: inherit;
					display: flex;
					align-items: end;
					margin-bottom: 0;

					small {
						font-size: clamp(17px, 17px + ((1vw - 3.2px) * 1.01), 28px);
						line-height: 1.4;
					}
				}

				.prefix,
				.suffix {
					font-size: clamp(17px, 17px + ((1vw - 3.2px) * 1.01), 28px);
					line-height: 1.4;
					color: inherit;
					align-self: end;
					margin-bottom: 0;
				}
			}

			.note {
				font-size: clamp(15px, 15px + ((1vw - 3.2px) * 0.739), 22px);
				margin-bottom: 20px;
			}

			.features {
				font-size: 18px;
				color: $contentColor;
				margin-bottom: 0;

				&:not(:last-child) {
					margin-bottom: 20px;
				}

				li {
					position: relative;
					padding-left: 1.875rem;
					margin-bottom: 0;

					&:not(:last-child) {
						margin-bottom: 10px;
					}

					&::before {
						content: url('data:image/svg+xml;base64,');
						display: inline-block;
						height: 100%;
						position: absolute;
						left: 0;
						top: 0.1em;
					}
				}
			}

			.bPlButton {
				width: 100%;
				margin-top: auto;
				margin-bottom: 0;
				justify-content: center;

				&:not(:last-child) {
					margin-bottom: 20px;
				}
			}

			&.bestValue {
				position: relative;
				color: #fff;
				background-color: $primaryColor;

				&::before {
					content: attr(data-best-text);
					font-size: 18px;
					line-height: 20px;
					font-weight: 700;
					writing-mode: vertical-lr;
					color: #fff;
					background-color: $secondaryColor;
					clip-path: polygon(0 0, 100% 0, 100% 100%, 50% 85%, 0 100%);
					position: absolute;
					top: -1px;
					right: 14px;
					padding: 15px 9px 30px 9px;
				}

				.planName,
				.priceWrap,
				.note,
				.features {
					color: #fff;
				}

				.features li::before {
					content: url('data:image/svg+xml;base64,');
				}

				.wp-element-button {
					border: 1px solid #fff;
				}
			}
		}
	}
}